html {
  font-size: 62.5%;
  box-sizing: border-box;
  margin: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Source Code Pro", sans-serif;
  font-size: 1.6rem;
  margin: 0;
  width: 100%;
  background-color: black;
  color: #ffffff;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 6.1rem;
}

p {
  font-size: 1.9rem;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 0rem 0;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.5rem;
}

img {
  display: block;
  max-width: 100%;
}

.header {
  background: linear-gradient(180deg, #9a4baa, rgb(62, 14, 66));
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.2rem 0.2rem 0.2rem 0.2rem rgb(209, 194, 211);
  }
}
.header h1 {
  color: #ffffff;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  padding: 1rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .header__nav {
    display: flex;
    gap: 7rem;
    margin-right: 10rem;
  }
}
.header__nav a {
  display: block;
  text-align: center;
  color: #ffffff;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .header__nav a {
    margin: 0;
  }
}
.header__nav a:hover {
  color: #dfe2e8;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(126, 2, 195);
  background: linear-gradient(270deg, rgba(126, 2, 195, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 10;
}
.contenido-video h2 {
  color: #ffffff;
  font-size: 7rem;
  margin: 0;
}
.contenido-video p {
  background-color: rgba(0, 0, 0, 0.3137254902);
  display: inline-block;
  padding: 1rem 2rem;
  color: #ffffff;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}

.sobre-festival {
  padding: 5rem 0rem;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  margin-bottom: 2rem;
}
.contenido-festival .fecha {
  color: #9a4baa;
  font-weight: 700;
}

.lineup {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 480px) {
  .lineup .description {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    padding: 5rem;
  }
}
.lineup .lineup__one {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.1882352941), rgba(0, 0, 0, 0.1882352941)), url(/build/img/blue.webp);
  color: #e73f3f;
}
.lineup .lineup__two {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.3137254902), rgba(0, 0, 0, 0.3137254902)), url(/build/img/green.webp);
  color: #39dae6;
}
.lineup .lineup__three {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.4392156863), rgba(0, 0, 0, 0.4392156863)), url(/build/img/red.webp);
  color: #f7c52f;
}
.lineup .lineup__description {
  display: none;
}

@media (max-width: 800px) {
  .lineup {
    margin: 0%;
  }
  .description {
    font-size: 1em;
  }
  .description:hover {
    font-size: 0.7em;
  }
  .lineup__fecha h3 {
    font-size: 1em;
    margin: 0;
    padding: 0;
  }
  .lineup__fecha p {
    font-weight: 500;
  }
  .lineup__description ul {
    font-weight: 500;
  }
}
.lineup__fecha h3 {
  font-size: 2em;
}
.lineup__fecha p {
  font-style: italic;
  font-weight: 700;
}

.description:hover {
  height: 30rem;
  justify-content: center;
  align-items: center;
}
.description:hover .lineup__description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.description:hover .lineup__description ul {
  list-style: none;
  font-weight: 900;
  font-size: 1.5em;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
  margin: 3em;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.7);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.modal {
  width: 3rem;
  height: 3rem;
  border-radius: 20%;
  background-color: #ffffff;
  display: grid;
  place-items: center;
  font-weight: 700;
  color: #000;
}
.modal:hover {
  cursor: pointer;
  background-color: #dfe2e8;
}

.price {
  text-align: center;
  margin: 5rem;
}

.price_table {
  display: flex;
  flex-wrap: wrap;
  gap: 2em;
  justify-content: space-evenly;
  align-items: center;
}

.price_element {
  background-color: #ffffff;
  text-align: center;
  width: 30rem;
  border-radius: 2rem;
}

.price_name {
  color: #f7c52f;
  margin-bottom: 0.5rem;
  font-weight: 700;
  letter-spacing: 1px;
  font-size: 1.7em;
}

.price_price {
  font-size: 1em;
  color: #f7c52f;
  margin: 0;
}

.price_items {
  display: grid;
  font-weight: 400;
  font-size: 1em;
  color: rgba(0, 0, 0, 0.3137254902);
  margin: 0;
}

.price_cta {
  background-color: #dfe2e8;
  display: block;
  padding: 10px 0px;
  border-radius: 15px;
  text-decoration: none;
  font-weight: 400;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.63);
}

.price_cta:hover {
  background-color: #f7c52f;
}

.footer {
  background-color: #dfe2e8;
  padding: 0.8rem;
  margin-top: 10rem;
}
.footer h4 {
  text-align: center;
  color: #9a4baa;
}